import React from "react";

// reactstrap components
import {
  Container,
  Row,
  Col,
  Form,
  Input,
  FormGroup,
  Label,
  Button,
  Modal,
  ModalBody,
} from "reactstrap";

import "react-accessible-accordion/dist/fancy-example.css";
import { useSelector, useDispatch } from "react-redux";
import { UpdateCartAction, RemoveCartAction } from "../js/actions/cartaction";
import { useState } from "react";
import axios from "axios";
import red from "../assets/img/illustrations/quarter-circle.png";
import yellow from "../assets/img/illustrations/quarter-yellow.png";
// core components
import DefaultNavbar from "components/Navbars/DefaultNavbar.js";
import DefaultFooter from "components/Footers/DefaultFooter.js";
import { Link, useHistory } from "react-router-dom";

import currency from "../js/methods/currency";
// import states from "../js/helpers/state.json";
import { toast } from "react-toastify";

function CheckOut() {
  const [modal, setModal] = React.useState(false);
  const toggle = () => setModal(!modal);

  const cartItems = useSelector((state) => state.userCart.items);

  const total = useSelector((state) => state.userCart.total);
  const commission = useSelector((state) => state.userCart.commission);
  const weight = useSelector((state) => state.userCart.weight);
  const shipping = useSelector((state) => state.userCart.shipping);
  // const fulfilment = useSelector((state) => state.userCart.fulfilment);
  const profileDetails = useSelector((state) => state.userDetails.userProfile);
  const [deliveryFee, setdeliveryFee] = useState(0);
  const [lgas, setLgas] = useState([]);
  const [lgaPrice, setLgaPrice] = useState([]);
  const dispatch = useDispatch();
  const token = localStorage.getItem("user-token");
  const [loading, setloading] = useState(false);
  const [detail, setDetail] = useState({
    name: "",
    surname: "",
    firstName: "",
    address: "",
    city: "",
    state: "",
    pickupPoint: "",
    phoneNumber: "",
    email: "",
    extraInstruction: "",
    // paymentMethod: "",
    policy: false,
    coupon: "",
    isScheduled: false,
    schedule_time: new Date(),
    contact: "",
    contact_email: "",
  });
  const [name, setname] = useState("");
  const [hidepaybutton, sethidepaybutton] = React.useState(false);
  const [showdetails, setshowdetails] = useState("desktop");
  const [showcart, setshowcart] = useState(false);
  const [showaddresses, setshowaddresses] = useState(false);
  const [allAddress, setAllAddress] = useState([]);
  const [defaultAddress, setDefaultAddress] = useState({
    contact_name: "",
    contact_email: "",
    address: "",
    phoneNumber: "",
    lga: "",
    shipping: "standard",
    shipping_fee: "",
    schedule_time: "",
  });
  const [chooseAddress, setchooseAddress] = useState(false);
  const [addAddress, setAddAddress] = useState(false);
  const [oldAddress, setOldAddress] = useState([]);
  const [coupon, setCoupon] = useState(null);
  const [discount, setDiscount] = useState(0);
  const [storeId, setStoreId] = useState(null);
  React.useEffect(() => {
    if (window.innerWidth < 768) {
      setshowdetails("mobile");
    } else {
      setshowdetails("desktop");
    }
  }, []);
  const history = useHistory();
  React.useEffect(() => {
    if (!token) {
      history.push("/login?redirect=/checkout");
    }
    return () => {};
  }, [history, token]);

  React.useEffect(() => {
    if (!cartItems.length) {
      sethidepaybutton(true);
    } else {
      sethidepaybutton(false);
      setStoreId(cartItems[0].store_id);
    }
    return () => {};
  }, [cartItems]);
  React.useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);
  React.useEffect(() => {
    if (profileDetails.address && profileDetails.address.length) {
      setOldAddress(profileDetails.address);
    }

    setDetail({
      surname: profileDetails.lastName,
      firstName: profileDetails.firstName,
      pickupPoint: "",
      city: "",
      state: "",
      phoneNumber: "",
      address: "",
      email: profileDetails.email,
      extraInstruction: "",
      schedule_time: new Date(),
      policy: false,
      coupon: "",
      shippingtype: "standard",
      deliverymethod: "home",
      isScheduled: false,
      contact: "",
      contact_email: "",
    });
  }, [profileDetails, oldAddress]);

  //handle coupon
  React.useEffect(() => {
    if (detail.coupon.length === 7) {
      axios
        .get(
          `${process.env.REACT_APP_API_URL}/api/get-coupon/${detail.coupon}`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then((res) => {
          if (res.status === 200) {
            setCoupon(res.data.discount);
          }
        })
        .catch(() => {
          toast.error("Invalid coupon ");
          setDetail({
            ...detail,
            coupon: "",
          });
        });
    }
  }, [detail, token]);

  React.useEffect(() => {
    setDiscount(total * coupon);
  }, [coupon, total]);

  function addNewAddress(e) {
    e.preventDefault();
    let address = allAddress;
    address.push(defaultAddress);

    setAllAddress(address);

    setDefaultAddress({
      contact_name: "",
      address: "",
      phoneNumber: "",
      lga: "",
      shipping: "standard",
      shipping_fee: "",
      schedule_time: "",
    });
    setshowaddresses(!showaddresses);
    handleDeliveryFee();
  }
  function removeAddress(index) {
    let address = allAddress;
    let newaddress = address.splice(index, 1);
    setAllAddress(newaddress);
    handleDeliveryFee();
  }
  function openAddressModal(val) {
    if (val === "addaddress") {
      setAddAddress(true);
      setchooseAddress(false);
    } else {
      setAddAddress(false);
      setchooseAddress(true);
    }
    setshowaddresses(!showaddresses);
  }

  function handleAdd(id) {
    var data = {
      action: "plus",
    };
    dispatch(UpdateCartAction(id, data));
  }
  function handleSubtract(id) {
    var data = {
      action: "subtract",
    };

    dispatch(UpdateCartAction(id, data));
  }
  function handleName(e) {
    setname(e);
  }
  function handleOnChange(e) {
    const value = e.target.value;
    if (value.length === 8 && e.target.name === "coupon") return;

    setDetail({
      ...detail,
      [e.target.name]: value,
    });
  }
  function handleDefault(e) {
    const value = e.target.value;

    setDefaultAddress({
      ...defaultAddress,
      [e.target.name]: value,
    });
  }
  function handlePolicy(e) {
    setDetail({
      ...detail,
      policy: e,
    });
  }

  function handleDeliveryType(e) {
    setDetail({
      ...detail,
      deliverymethod: e.target.value,
    });
  }
  function handleDeliveryFee() {
    let amount = allAddress
      .map((item) => {
        return item.shipping_fee;
      })
      .reduce((a, b) => a + b);
    setdeliveryFee(amount);
  }

  function handleScheduleTime(e) {
    setDetail({
      ...detail,
      schedule_time: e.target.value,
    });
  }
  function handleAddress(val) {
    setDefaultAddress({
      address: val.address,
      lga: val.lga,
      contact_email: val.contact_email,
      phoneNumber: val.phoneNumber,
      contact_name: val.contact_name,
      shipping: "",
      schedule_time: "",
    });

    setAddAddress(true);
    setchooseAddress(false);
  }

  React.useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/api/get-lgas`).then((res) => {
      if (res.status === 200) {
        setLgas(res.data);
      }
    });
  }, []);

  React.useEffect(() => {
    if (!defaultAddress.lga || !storeId) return;
    axios
      .get(
        `${process.env.REACT_APP_API_URL}/api/get-lga-price/${defaultAddress.lga}/${storeId}`
      )
      .then((res) => {
        if (res.status === 200) {
          setLgaPrice(res.data);

        }
      });
  }, [defaultAddress.lga, storeId]);

  React.useEffect(() => {

    switch (defaultAddress.shipping) {
      case "standard":
        setDefaultAddress((d) => {
          return {
            ...d,
            shipping_fee: lgaPrice.standard_fee,
          };
        });
        break;
      case "express":
        setDefaultAddress((d) => {
          return {
            ...d,
            shipping_fee: lgaPrice.express_fee,
          };
        });
        break;
      case "scheduled":
        setDefaultAddress(d =>{
         return {
          ...d,
          shipping_fee: lgaPrice.scheduled_fee,
        }
        });
        break;
      default:
        break;
    }
  }, [defaultAddress.shipping, lgaPrice]);

  function handleSubmit(e) {
    e.preventDefault();
    setloading(true);
    if (!detail.policy) {
      toast.info("Agree to our policy");
      setloading(false);
      return;
    }
    if (!allAddress.length) {
      toast.info("Address missing");
      setloading(false);
      return;
    }
    detail.commission = commission;
    detail.shipping = shipping;
    detail.allAddress = allAddress;
    detail.title = "Order";
    detail.name = name;
    axios
      .post(`${process.env.REACT_APP_API_URL}/api/user/orders`, detail, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.status === 201) {
          window.location.href = res.data.data.authorization_url;
          setloading(false);
        }
      })
      .catch((err) => {
        setloading(false);
        setDetail({
          ...detail,
          policy: false,
        });
        toast.error(err.response.data.message);
      });
  }

  function deleteCart(id) {
    dispatch(RemoveCartAction(id));
  }
  function handleCart() {
    setshowcart(!showcart);
  }

  return (
    <>
      <DefaultNavbar />
      <div className="wrapper">
        <section className="section section-checkout">
          <Container>
            {showdetails === "desktop" ? (
              <Row className="flex-column-reverse flex-md-row">
                <Col lg="7">
                  <Form
                    className="checkout-form"
                    onSubmit={(e) => handleSubmit(e)}
                  >
                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Personal Details</div>
                      </Col>
                      <Col lg="9">
                        <div className="form-row">
                          <FormGroup className="col-lg-5">
                            <label>Surname</label>
                            <Input
                              placeholder="Surname"
                              defaultValue={detail.surname}
                              readOnly
                              type="text"
                              name="surname"
                            />
                          </FormGroup>
                          <FormGroup className="col-lg-5">
                            <label>First Name</label>
                            <Input
                              placeholder="First Name"
                              defaultValue={detail.firstName}
                              readOnly
                              type="text"
                              name="firstName"
                            />
                          </FormGroup>
                        </div>
                      </Col>
                    </Row>
                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Shipping Details</div>
                      </Col>
                      <Col lg="9">
                        <div>
                          <div className="mb-4">
                            <FormGroup check>
                              <Label check className="delivery">
                                <Input
                                  type="radio"
                                  name="deliverymethod"
                                  checked={detail.deliverymethod === "home"}
                                  value="home"
                                  onChange={(e) => handleDeliveryType(e)}
                                />{" "}
                                <span>Have it delivered to your home</span>
                              </Label>
                            </FormGroup>

                            <div
                              className="form-row"
                              style={{
                                opacity:
                                  detail.deliverymethod === "pickup" ? 0.4 : "",
                              }}
                            >
                              <div className="col-lg-10 border rounded mt-2">
                                {allAddress &&
                                  allAddress.map((item, index) => (
                                    <div
                                      className="w-100 position-relative p-2"
                                      key={index}
                                    >
                                      <div className=" w-100">
                                        <label className="mb-0 text-sm text-primary opacity-60">
                                          Address {index + 1}
                                        </label>
                                        <p className="mb-0 text-md">
                                          {" "}
                                          {item.address}
                                        </p>
                                      </div>

                                      {item.address && (
                                        <div className="mt-0 ">
                                          <div className="text-capitalize text-md d-flex justify-content-between">
                                            <span> {item.contact_name} </span>
                                            <span>
                                              {" "}
                                              {item.shipping} shipping
                                            </span>
                                          </div>
                                        </div>
                                      )}

                                      {allAddress.length > 1 && (
                                        <i
                                          onClick={() => removeAddress(index)}
                                          className="fa fa-times-circle position-absolute text-md"
                                          style={{ top: "5px", right: "8px" }}
                                          aria-hidden="true"
                                        ></i>
                                      )}
                                      {index !== allAddress.length - 1 && (
                                        <hr className="my-1" />
                                      )}
                                    </div>
                                  ))}

                                {!allAddress.length && (
                                  <p className="mb-0 text-md opacity-50">
                                    No address available
                                  </p>
                                )}
                              </div>
                              <div className="text-right w-100 col-lg-10">
                                <span
                                  className="text-primary text-sm mt-1 mr-3 cursor-pointer ml-auto"
                                  style={{
                                    cursor: "pointer",
                                    fontSize: "12px",
                                  }}
                                  onClick={() =>
                                    openAddressModal("chooseaddress")
                                  }
                                >
                                  {" "}
                                  Choose address
                                </span>
                                <span
                                  className="text-primary text-sm mt-1 cursor-pointer ml-auto"
                                  style={{
                                    cursor: "pointer",
                                    fontSize: "12px",
                                  }}
                                  onClick={() => openAddressModal("addaddress")}
                                >
                                  {" "}
                                  Add address
                                </span>
                              </div>
                            </div>
                          </div>

                          <div className="mb-4">
                            <FormGroup check>
                              <Label check className="delivery">
                                <Input
                                  type="radio"
                                  name="deliverymethod"
                                  checked={detail.deliverymethod === "pickup"}
                                  value="pickup"
                                  onChange={(e) => handleDeliveryType(e)}
                                />{" "}
                                <span>
                                  Or have it dropped at a pick up location
                                </span>
                              </Label>
                            </FormGroup>
                            <div
                              style={{
                                opacity:
                                  detail.deliverymethod === "home" ? 0.4 : "",
                              }}
                            >
                              <div className="form-row">
                                <FormGroup className="col-lg-10">
                                  <label>
                                    Select pick up point nearest to you
                                  </label>
                                  <Input
                                    placeholder="PickUp Point"
                                    value={detail.pickupPoint}
                                    onChange={(e) => {
                                      handleOnChange(e);
                                    }}
                                    name="pickupPoint"
                                    type="select"
                                  >
                                    <option defaultValue="">
                                      4, Maijala street, Ikeja GRA, Ikeja, Lagos
                                    </option>
                                  </Input>
                                </FormGroup>
                              </div>
                            </div>
                          </div>

                          <div className="mb-4">
                            <div>
                              {detail.shippingtype === "scheduled" ? (
                                <FormGroup>
                                  <Label>
                                    Schedule Date
                                    <Input
                                      type="datetime-local"
                                      name="schedule_time"
                                      value={detail.schedule_time}
                                      onChange={(e) => handleScheduleTime(e)}
                                    />{" "}
                                  </Label>
                                </FormGroup>
                              ) : (
                                ""
                              )}
                            </div>
                          </div>
                        </div>
                      </Col>
                    </Row>

                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Extra Instructions</div>
                      </Col>
                      <Col lg="9">
                        <div>
                          <div className="form-row">
                            <FormGroup className="col-lg-10">
                              <Input
                                value={detail.extraInstruction}
                                onChange={(e) => {
                                  handleOnChange(e);
                                }}
                                placeholder="Anything else you want us to know?"
                                rows="3"
                                type="textarea"
                                name="extraInstruction"
                              />
                            </FormGroup>
                          </div>
                        </div>
                      </Col>
                    </Row>
                    <Row className="mb-4">
                      <Col lg="3">
                        <div className="h6">Payment Method</div>
                      </Col>
                      <Col lg="9">
                        <div className="row">
                          <div className="col-lg-10">
                            <div className="discount-block">
                              <FormGroup className="row">
                                <Label sm="6">Apply discount code</Label>
                                <Col sm="6">
                                  <Input
                                    className="form-control-plaintext"
                                    value={detail.coupon}
                                    placeholder="BEIDNK24"
                                    type="text"
                                    onChange={(e) => {
                                      handleOnChange(e);
                                    }}
                                    name="coupon"
                                  />
                                </Col>
                              </FormGroup>
                              {discount > 0 && (
                                <div className="hint">
                                  This code gives you {currency(discount)} off
                                  your total purchase
                                </div>
                              )}
                            </div>
                            <div className="policy-form">
                              <FormGroup check>
                                <Label check>
                                  <Input
                                    type="checkbox"
                                    onChange={(e) =>
                                      handlePolicy(e.target.checked)
                                    }
                                    name="policy"
                                    checked={detail.policy}
                                  />
                                  <span className="form-check-sign">
                                    <span className="check"></span>
                                  </span>
                                  By proceeding, you hereby agree to our
                                  Policies,{" "}
                                  <Link to="/terms-of-service">
                                    Terms and Conditions
                                  </Link>
                                  .{" "}
                                </Label>
                              </FormGroup>
                            </div>
                          </div>
                        </div>
                      </Col>
                    </Row>
                    {hidepaybutton ? (
                      ""
                    ) : (
                      <Row className="mb-4">
                        <Col lg="3" />
                        <Col lg="9">
                          <div>
                            <div className="form-row">
                              <div className="col-lg-10">
                                <Button
                                  className="bg-primary-gradient"
                                  color="primary"
                                  size="lg"
                                  block
                                  type="submit"
                                  disabled={loading}
                                >
                                  {loading ? (
                                    <i
                                      className="fa fa-spinner fa-spin"
                                      aria-hidden="true"
                                    ></i>
                                  ) : (
                                    "Pay"
                                  )}
                                </Button>
                              </div>
                            </div>
                          </div>
                        </Col>
                      </Row>
                    )}
                  </Form>
                </Col>
                <Col lg="5">
                  {cartItems ? (
                    <div className="order-summary">
                      <div className="mb-3 order-name">
                        <Label>Order name</Label>
                        <Input
                          onChange={(e) => handleName(e.target.value)}
                          value={name}
                          placeholder="Provide your order name"
                        />
                      </div>
                      <div className="cart-block">
                        {cartItems.map((item) => (
                          <div className="cart-item-block" key={item.id}>
                            <img
                              alt="..."
                              className="product-img"
                              src={item.image}
                            />
                            <div style={{ flex: 1 }}>
                              <div className="d-flex justify-content-between">
                                <div>
                                  <p className="product-name">
                                    {item.product_name}
                                  </p>
                                  <p className="product-location">
                                    <i className="fa fa-map-marker-alt mr-2" />
                                    {item.store_name}
                                  </p>
                                </div>
                                <Button
                                  onClick={() => deleteCart(item.id)}
                                  className="btn-icon btn-link mt-0"
                                  color="danger"
                                  type="button"
                                  size="sm"
                                >
                                  <i className="fa fa-trash-alt" />
                                </Button>
                              </div>

                              <div className="d-flex align-items-center justify-content-between mt-2">
                                <p className="product-price h6">
                                  {currency(item.price)}
                                </p>
                                <div className="action-buttons d-flex align-items-center justify-content-between">
                                  <Button
                                    onClick={() => handleSubtract(item.id)}
                                    className="btn-icon m-0 btn-round"
                                    color=""
                                    disabled={item.quantity === 1}
                                    type="button"
                                    size="sm"
                                  >
                                    <i className="now-ui-icons ui-1_simple-delete" />
                                  </Button>
                                  <span className="product-amount">
                                    {item.quantity}
                                  </span>
                                  <Button
                                    onClick={() => handleAdd(item.id)}
                                    className="btn-icon m-0 btn-round"
                                    color=""
                                    type="button"
                                    size="sm"
                                  >
                                    <i className="now-ui-icons ui-1_simple-add" />
                                  </Button>
                                </div>
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>

                      <div className="mt-5">
                        <div className="d-flex align-items-center justify-content-between">
                          <p className="mb-1">Total weight</p>
                          <p className=" mb-1">
                            {allAddress.length > 1
                              ? weight * allAddress.length
                              : weight}
                            kg
                          </p>
                        </div>
                        <div className="d-flex align-items-center justify-content-between">
                          <p className="mb-1">Delivery fee</p>
                          <p className=" mb-1">{currency(deliveryFee)}</p>
                        </div>
                        <div className="d-flex align-items-center justify-content-between">
                          <p className="mb-1">Delivery address</p>
                          <p className=" mb-1">x{allAddress.length}</p>
                        </div>
                        <div className="d-flex align-items-center justify-content-between">
                          <p className="mb-1">Coupon Discount</p>
                          <p className=" mb-1">{currency(discount)}</p>
                        </div>
                        <div className="d-flex align-items-center justify-content-between">
                          <p className="mb-1">Subtotal </p>
                          <p className=" mb-1">
                            {currency(
                              allAddress.length > 1
                                ? total * allAddress.length
                                : total
                            )}
                          </p>
                        </div>
                      </div>
                      <div className="d-flex align-items-center justify-content-between total-block">
                        <p className="m-0">Total amount</p>
                        <h5 className="font-weight-bold m-0">
                          {currency(
                            allAddress.length > 1
                              ? total * allAddress.length +
                                  deliveryFee -
                                  discount
                              : total + deliveryFee - discount
                          )}
                        </h5>
                      </div>
                    </div>
                  ) : (
                    ""
                  )}
                </Col>
              </Row>
            ) : (
              <div>
                {showcart ? (
                  <div>
                    <div className="mb-3">
                      {" "}
                      <small className="" onClick={() => handleCart()}>
                        {" "}
                        <i
                          className="fa fa-arrow-left"
                          aria-hidden="true"
                        ></i>{" "}
                        Back
                      </small>
                    </div>
                    <Form
                      className="checkout-form"
                      onSubmit={(e) => handleSubmit(e)}
                    >
                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Personal Details</div>
                        </Col>
                        <Col lg="9">
                          <div className="form-row">
                            <FormGroup className="col-lg-5">
                              <label>Surname</label>
                              <Input
                                placeholder="Surname"
                                defaultValue={detail.surname}
                                readOnly
                                type="text"
                                name="surname"
                              />
                            </FormGroup>
                            <FormGroup className="col-lg-5">
                              <label>First Name</label>
                              <Input
                                placeholder="First Name"
                                defaultValue={detail.firstName}
                                readOnly
                                type="text"
                                name="firstName"
                              />
                            </FormGroup>
                          </div>
                        </Col>
                      </Row>
                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Shipping Details</div>
                        </Col>
                        <Col lg="9">
                          <div>
                            <div>
                              <FormGroup check>
                                <Label check className="delivery">
                                  <Input
                                    type="radio"
                                    name="deliverymethod"
                                    checked={detail.deliverymethod === "home"}
                                    value="home"
                                    onChange={(e) => handleDeliveryType(e)}
                                  />{" "}
                                  <span>Have it delivered to your home</span>
                                </Label>
                              </FormGroup>

                              <div
                                className="form-row"
                                style={{
                                  opacity:
                                    detail.deliverymethod === "pickup"
                                      ? 0.4
                                      : "",
                                }}
                              >
                                <div className="col-lg-10 border rounded mt-2">
                                  {allAddress &&
                                    allAddress.map((item, index) => (
                                      <div
                                        className="w-100 position-relative p-2"
                                        key={index}
                                      >
                                        <div className=" w-100">
                                          <label className="mb-0 text-sm text-primary opacity-60">
                                            Address {index + 1}
                                          </label>
                                          <p className="mb-0 text-md">
                                            {" "}
                                            {item.address}
                                          </p>
                                        </div>

                                        {item.address && (
                                          <div className="mt-0 ">
                                            <div className="text-capitalize text-md d-flex justify-content-between">
                                              <span> {item.contact_name} </span>
                                              <span>
                                                {" "}
                                                {item.shipping} shipping
                                              </span>
                                            </div>
                                          </div>
                                        )}

                                        {allAddress.length > 1 && (
                                          <i
                                            onClick={() => removeAddress(index)}
                                            className="fa fa-times-circle position-absolute text-md"
                                            style={{ top: "5px", right: "8px" }}
                                            aria-hidden="true"
                                          ></i>
                                        )}
                                        {index !== allAddress.length - 1 && (
                                          <hr className="my-1" />
                                        )}
                                      </div>
                                    ))}

                                  {!allAddress.length && (
                                    <p className="mb-0 text-md opacity-50">
                                      No address available
                                    </p>
                                  )}
                                </div>
                                <div className="text-right w-100 col-lg-10">
                                  <span
                                    className="text-primary text-sm mt-1 mr-3 cursor-pointer ml-auto"
                                    style={{
                                      cursor: "pointer",
                                      fontSize: "12px",
                                    }}
                                    onClick={() =>
                                      openAddressModal("chooseaddress")
                                    }
                                  >
                                    {" "}
                                    Choose address
                                  </span>
                                  <span
                                    className="text-primary text-sm mt-1 cursor-pointer ml-auto"
                                    style={{
                                      cursor: "pointer",
                                      fontSize: "12px",
                                    }}
                                    onClick={() =>
                                      openAddressModal("addaddress")
                                    }
                                  >
                                    {" "}
                                    Add address
                                  </span>
                                </div>
                              </div>
                            </div>

                            <div className="mb-4 mt-4">
                              <FormGroup check>
                                <Label check className="delivery">
                                  <Input
                                    type="radio"
                                    name="deliverymethod"
                                    checked={detail.deliverymethod === "pickup"}
                                    value="pickup"
                                    onChange={(e) => handleDeliveryType(e)}
                                  />{" "}
                                  <span>
                                    Or have it dropped at a pick up location
                                  </span>
                                </Label>
                              </FormGroup>
                              <div
                                style={{
                                  opacity:
                                    detail.deliverymethod === "home" ? 0.4 : "",
                                }}
                              >
                                <div className="form-row">
                                  <FormGroup className="col-lg-10">
                                    <label>
                                      Select pick up point nearest to you
                                    </label>
                                    <Input
                                      placeholder="PickUp Point"
                                      value={detail.pickupPoint}
                                      onChange={(e) => {
                                        handleOnChange(e);
                                      }}
                                      name="pickupPoint"
                                      type="select"
                                    >
                                      <option defaultValue="">
                                        4, Maijala street, Ikeja GRA, Ikeja,
                                        Lagos
                                      </option>
                                    </Input>
                                  </FormGroup>
                                </div>
                              </div>
                            </div>
                          </div>
                        </Col>
                      </Row>

                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Extra Instructions</div>
                        </Col>
                        <Col lg="9">
                          <div>
                            <div className="form-row">
                              <FormGroup className="col-lg-10">
                                <Input
                                  value={detail.extraInstruction}
                                  onChange={(e) => {
                                    handleOnChange(e);
                                  }}
                                  placeholder="Anything else you want us to know?"
                                  rows="3"
                                  type="textarea"
                                  name="extraInstruction"
                                />
                              </FormGroup>
                            </div>
                          </div>
                        </Col>
                      </Row>
                      <Row className="mb-4">
                        <Col lg="3">
                          <div className="h6">Payment Method</div>
                        </Col>
                        <Col lg="9">
                          <div className="row">
                            <div className="col-lg-10">
                              <div className="discount-block">
                                <FormGroup className="row">
                                  <Label sm="6">Apply discount code</Label>
                                  <Col sm="6">
                                    <Input
                                      className="form-control-plaintext"
                                      value={detail.coupon}
                                      placeholder="BEIDNK24"
                                      type="text"
                                      onChange={(e) => {
                                        handleOnChange(e);
                                      }}
                                      name="coupon"
                                    />
                                  </Col>
                                </FormGroup>
                                {discount > 0 && (
                                  <div className="hint">
                                    This code gives you {currency(discount)} off
                                    your total purchase
                                  </div>
                                )}
                              </div>
                              <div className="policy-form">
                                <FormGroup check>
                                  <Label check>
                                    <Input
                                      type="checkbox"
                                      onChange={(e) =>
                                        handlePolicy(e.target.checked)
                                      }
                                      name="policy"
                                      defaultValue={detail.policy}
                                    />
                                    <span className="form-check-sign">
                                      <span className="check"></span>
                                    </span>
                                    By proceeding, you hereby agree to our
                                    Policies,{" "}
                                    <Link to="/terms-of-service">
                                      Terms and Conditions
                                    </Link>
                                    .{" "}
                                  </Label>
                                </FormGroup>
                              </div>
                            </div>
                          </div>
                        </Col>
                      </Row>
                      {hidepaybutton ? (
                        ""
                      ) : (
                        <Row className="mb-4">
                          <Col lg="3" />
                          <Col lg="9">
                            <div>
                              <div className="form-row">
                                <div className="col-lg-10">
                                  <Button
                                    className="bg-primary-gradient"
                                    color="primary"
                                    size="lg"
                                    block
                                    type="submit"
                                    disabled={loading}
                                  >
                                    {loading ? (
                                      <i
                                        className="fa fa-spinner fa-spin"
                                        aria-hidden="true"
                                      ></i>
                                    ) : (
                                      "Pay"
                                    )}
                                  </Button>
                                </div>
                              </div>
                            </div>
                          </Col>
                        </Row>
                      )}
                    </Form>
                  </div>
                ) : (
                  <div>
                    {cartItems.length ? (
                      <div className="order-summary">
                        <div className="mb-3 order-name">
                          <Label>Order title</Label>
                          <Input
                            onChange={(e) => handleName(e.target.value)}
                            value={name}
                            placeholder="Provide a title for this order"
                          />
                        </div>
                        <div className="cart-block">
                          {cartItems.map((item) => (
                            <div className="cart-item-block" key={item.id}>
                              <img
                                alt="..."
                                className="product-img"
                                src={item.image}
                              />
                              <div style={{ flex: 1 }}>
                                <div className="d-flex justify-content-between">
                                  <div>
                                    <p className="product-name">
                                      {item.product_name}
                                    </p>
                                    <p className="product-location">
                                      <i className="fa fa-map-marker-alt mr-2" />
                                      {item.store_name}
                                    </p>
                                  </div>
                                  <Button
                                    onClick={() => deleteCart(item.id)}
                                    className="btn-icon btn-link mt-0"
                                    color="danger"
                                    type="button"
                                    size="sm"
                                  >
                                    <i className="fa fa-trash-alt" />
                                  </Button>
                                </div>

                                <div className="d-flex align-items-center justify-content-between mt-2">
                                  <p className="product-price h6">
                                    {currency(
                                      item.sales_price
                                        ? item.sales_price
                                        : item.price
                                    )}
                                  </p>
                                  <div className="action-buttons d-flex align-items-center justify-content-between">
                                    <Button
                                      onClick={() => handleSubtract(item.id)}
                                      className="btn-icon m-0 btn-round"
                                      color=""
                                      disabled={item.quantity === 1}
                                      type="button"
                                      size="sm"
                                    >
                                      <i className="now-ui-icons ui-1_simple-delete" />
                                    </Button>
                                    <span className="product-amount">
                                      {item.quantity}
                                    </span>
                                    <Button
                                      onClick={() => handleAdd(item.id)}
                                      className="btn-icon m-0 btn-round"
                                      color=""
                                      type="button"
                                      size="sm"
                                    >
                                      <i className="now-ui-icons ui-1_simple-add" />
                                    </Button>
                                  </div>
                                </div>
                              </div>
                            </div>
                          ))}
                        </div>

                        <div className="mt-5">
                          <div className="d-flex align-items-center justify-content-between">
                            <p className="mb-1">Total weight</p>
                            <p className=" mb-1">
                              {allAddress.length > 1
                                ? weight * allAddress.length
                                : weight}
                              kg
                            </p>
                          </div>
                          <div className="d-flex align-items-center justify-content-between">
                            <p className="mb-1">Delivery fee</p>
                            <p className=" mb-1">{currency(deliveryFee)}</p>
                          </div>
                          <div className="d-flex align-items-center justify-content-between">
                            <p className="mb-1">Delivery address</p>
                            <p className="mb-1">x{allAddress.length}</p>
                          </div>
                          <div className="d-flex align-items-center justify-content-between">
                            <p className="mb-1">Coupon Discount</p>
                            <p className=" mb-1">{currency(discount)}</p>
                          </div>
                          <div className="d-flex align-items-center justify-content-between">
                            <p className="mb-1">Subtotal </p>
                            <p className=" mb-1">
                              {currency(
                                allAddress.length > 1
                                  ? total * allAddress.length
                                  : total
                              )}
                            </p>
                          </div>
                        </div>
                        <div className="d-flex align-items-center justify-content-between total-block">
                          <p className="m-0">Total amount</p>
                          <h5 className="font-weight-bold m-0">
                            {currency(
                              allAddress.length > 1
                                ? total * allAddress.length +
                                    deliveryFee -
                                    discount
                                : total + deliveryFee - discount
                            )}
                          </h5>
                        </div>
                      </div>
                    ) : (
                      <div className="text-center">No item in cart</div>
                    )}

                    <Button
                      onClick={() => handleCart()}
                      type="button"
                      color="primary"
                      block
                    >
                      Proceed
                    </Button>
                  </div>
                )}
              </div>
            )}
          </Container>
          <Modal
            isOpen={modal}
            toggle={toggle}
            centered
            className="section-product-details-modal"
          >
            <ModalBody className=" position-relative">
              <div>
                <div className="p-5">
                  <div className=" text-center">
                    <i
                      className="fa fa-check-circle fa-4x text-success"
                      aria-hidden="true"
                    ></i>{" "}
                    <br />
                    <strong>Payment successful</strong> <br />
                    <small>Your receipt will be sent to you via mail</small>
                  </div>
                </div>

                <img alt="red" src={red} className="modal-red" />
                <img alt="yellow" src={yellow} className="modal-yellow" />
              </div>
            </ModalBody>
          </Modal>

          <Modal
            isOpen={showaddresses}
            toggle={openAddressModal}
            centered
            className="section-product-details-modal"
          >
            <ModalBody className=" position-relative">
              <div className="p-3">
                <div className="d-flex justify-content-between align-items-center mb-3">
                  {chooseAddress ? (
                    <h5 className="my-0 text-primary">Select an address</h5>
                  ) : (
                    <h5 className="my-0 text-primary">Add an address</h5>
                  )}
                  <span
                    className="position-absolute"
                    style={{ zIndex: "8", top: "10px", right: "10px" }}
                    onClick={() => openAddressModal()}
                  >
                    {" "}
                    <i className="fas fa-times "></i>
                  </span>
                </div>
                {chooseAddress && (
                  <div>
                    {oldAddress.length ? (
                      <div className="px-3">
                        {oldAddress.map((val, index) => (
                          <div key={index}>
                            {" "}
                            <FormGroup className="mb-0">
                              <Label>
                                <Input
                                  type="radio"
                                  onChange={() => handleAddress(val)}
                                  name="address"
                                />

                                <p className="mb-0"> {val.address}</p>
                                <p className="mb-0">{val.contact_name}</p>
                                <p>{val.phoneNumber}</p>
                              </Label>
                            </FormGroup>
                          </div>
                        ))}
                      </div>
                    ) : (
                      <div className="text-center">No address found</div>
                    )}
                  </div>
                )}

                {addAddress && (
                  <Form onSubmit={(e) => addNewAddress(e)} className="w-100">
                    <div className="form-row">
                      <FormGroup className="col-lg-12">
                        <label>Address</label>
                        <Input
                          placeholder="Address"
                          onChange={(e) => {
                            handleDefault(e);
                          }}
                          type="text"
                          name="address"
                          required
                          value={defaultAddress.address}
                        />
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <FormGroup className="col-lg-12">
                        <label>Email</label>
                        <Input
                          placeholder="Email"
                          onChange={(e) => {
                            handleDefault(e);
                          }}
                          type="email"
                          name="contact_email"
                          required
                          value={defaultAddress.email}
                        />
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>Contact name</label>
                        <Input
                          placeholder="Contact name"
                          onChange={(e) => {
                            handleDefault(e);
                          }}
                          required
                          type="text"
                          name="contact_name"
                          value={defaultAddress.contact_name}
                        ></Input>
                      </FormGroup>
                      <FormGroup className="col-lg-6">
                        <label>Contact phone</label>
                        <Input
                          placeholder="Contact phone"
                          onChange={(e) => {
                            handleDefault(e);
                          }}
                          type="number"
                          name="phoneNumber"
                          required
                          value={defaultAddress.phoneNumber}
                        ></Input>
                      </FormGroup>
                    </div>

                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>Lga</label>
                        <Input
                          placeholder="Lga"
                          required
                          value={defaultAddress.lga}
                          onChange={(e) => {
                            handleDefault(e);
                          }}
                          type="select"
                          name="lga"
                        >
                          <option value="">Choose...</option>
                          {lgas &&
                            lgas.map((item) => (
                              <option value={item.id} key={item.id}>
                                {item.lga}
                              </option>
                            ))}
                        </Input>
                      </FormGroup>
                      <FormGroup className="col-lg-6">
                        <label>Shipping fee</label>
                        {defaultAddress.shipping_fee && (
                          <p>{currency(defaultAddress.shipping_fee)}</p>
                        )}
                      </FormGroup>
                    </div>

                    <div className="mt-4 mb-1">
                      <label className="mb-0">Shipping type</label>
                      <div className="d-flex">
                        <FormGroup check className="mr-3">
                          <Label check className="shipping">
                            <Input
                              checked={defaultAddress.shipping === "standard"}
                              type="radio"
                              name="shipping"
                              value="standard"
                              onChange={(e) => handleDefault(e)}
                            />{" "}
                            Standard
                          </Label>
                        </FormGroup>
                        <FormGroup check className="mr-3">
                          <Label check className="shipping">
                            <Input
                              checked={defaultAddress.shipping === "express"}
                              type="radio"
                              name="shipping"
                              value="express"
                              onChange={(e) => handleDefault(e)}
                            />{" "}
                            Express
                          </Label>
                        </FormGroup>
                        <FormGroup check>
                          <Label check className="shipping">
                            <Input
                              checked={defaultAddress.shipping === "scheduled"}
                              type="radio"
                              name="shipping"
                              value="scheduled"
                              onChange={(e) => handleDefault(e)}
                            />{" "}
                            Schedule
                          </Label>
                        </FormGroup>
                      </div>
                    </div>

                    <div className="mb-3">
                      <div>
                        {defaultAddress.shipping === "scheduled" ? (
                          <FormGroup>
                            <Label>
                              Schedule Date
                              <Input
                                type="datetime-local"
                                name="schedule_time"
                                value={defaultAddress.schedule_time}
                                onChange={(e) => handleDefault(e)}
                              />{" "}
                            </Label>
                          </FormGroup>
                        ) : (
                          ""
                        )}
                      </div>
                    </div>

                    <div>
                      <Button
                        className="btn btn-primary btn-block"
                        type="submit"
                      >
                        ADD ADDRESS
                      </Button>
                    </div>
                  </Form>
                )}

                <img alt="red" src={red} className="modal-red" />
                <img alt="yellow" src={yellow} className="modal-yellow" />
              </div>
            </ModalBody>
          </Modal>
        </section>

        <DefaultFooter />
      </div>
    </>
  );
}

export default CheckOut;
