function OrderReceipt() {
  const location = useLocation();
  const [detail, setdetail] = React.useState({});
  const [status, setstatus] = React.useState("loading");
  const [type, settype] = React.useState("");
  React.useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);

  React.useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const token = localStorage.getItem("user-token");
    const ref = queryParams.get("reference");
    axios
      .get(`${process.env.REACT_APP_API_URL}/api/transaction/verify/${ref}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          setstatus(res.data.status);
          settype(res.data.type);
          if (res.data.status) {
            setdetail(res.data.data);
            if (res.data.new_token) {
              localStorage.setItem("payviame-token", res.data.new_token);
            }
          }
        }
      });
    return () => {
      setdetail({});
    };
  }, [location.search]);

  return (
    <>
      <DefaultNavbar />
      <div className="wrapper">
        <section className="section section-checkout">
          <Container>
            <Row>
              {status === "loading" ? (
                <div className="text-center p-5 w-100">
                  <div className="fa-5x b-3">
                    <i className="fas fa-spinner fa-pulse"></i>
                  </div>
                  <span className="h6">Verifying transaction</span>
                </div>
              ) : (
                ""
              )}
              {status === true ? (
                <Col className="ml-auto mr-auto" lg="6">
                  {type === "order" ? (
                    <div>
                      <div className="text-center mb-4">
                        <p className="font-weight-bold">Order Receipt</p>
                      </div>
                      {detail.order ? (
                        <div className="order-summary">
                          <p className="table-title mt-0">
                            {detail.order.name}
                          </p>
                          <div className="checkout-table">
                            <p className="small font-weight-bold">Order ID</p>
                            <p className="small">{detail.order.order_no}</p>
                          </div>
                          <div className="checkout-table mb-1">
                            <p className="small font-weight-bold mb-1">
                              Delivery Method
                            </p>
                            <p className="small text-capitalize mb-1">
                              {detail.order.shipping_method}
                            </p>
                          </div>
                          {detail.order.isScheduled ? (
                            <div className="checkout-table">
                              <p className="small font-weight-bold mb-1">
                                Delivery Schedule
                              </p>
                              <p className="small mb-1">
                                {moment(detail.order.schedule_time).format(
                                  "DD/MM/YYYY"
                                )}
                              </p>
                            </div>
                          ) : (
                            ""
                          )}
                          <div className="checkout-table">
                            <p className="small font-weight-bold">
                              {detail.order.items}{" "}
                              {detail.order.items > 1 ? "items" : "item"}
                            </p>
                          </div>
                          <div
                            className="cart-block"
                            style={{ maxHeight: 300 + "px", overflowY: "auto" }}
                          >
                            {detail.order.orderhistories.map((item) => (
                              <div className="cart-item-block" key={item.id}>
                                <img
                                  alt="..."
                                  className="product-img"
                                  src={item.image}
                                />
                                <div className="w-100">
                                  <div className="d-flex justify-content-between">
                                    <div className="w-100">
                                      <p className="product-name">
                                        {item.product_name}
                                      </p>
                                      <p className="product-location">
                                        <i className="fa fa-map-marker-alt mr-2" />
                                        {item.store_name}
                                      </p>
                                    </div>
                                  </div>

                                  <div className="d-flex align-items-center justify-content-between w-100 mt-2">
                                    <p className="product-price h6">
                                      {currency(
                                        item.sales_price
                                          ? item.sales_price
                                          : item.price
                                      )}
                                    </p>

                                    <span className="product-amount">
                                      {item.quantity}
                                    </span>
                                  </div>
                                </div>
                              </div>
                            ))}
                          </div>
                          <div>
                            <p className="table-title">Delivery Address</p>
                            <p className="small">
                              {detail.order.orderinfo.shipping_address}
                            </p>
                          </div>
                          <div>
                            <p className="table-title">Delivery Instructions</p>
                            <p className="small">
                              {detail.order.orderinfo.extra_instruction}
                            </p>
                          </div>

                          <div className="mt-5">
                            <p className="table-title">Payment Breakdown</p>
                            <div className="receipt-total">
                              <div className="d-flex align-items-center justify-content-between">
                                <p className="mb-2 font-weight-bold">Total</p>
                                <p className="font-weight-bold mb-1">
                                  {" "}
                                  {currency(detail.order.grand_total)}
                                </p>
                              </div>
                              <div className="d-flex align-items-center justify-content-between">
                                <p className="mb-2">Order Amount</p>
                                <p className="mb-2">
                                  {" "}
                                  {currency(detail.order.total_amount)}
                                </p>
                              </div>
                              <div className="d-flex align-items-center justify-content-between">
                                <p className="mb-2">Delivery</p>
                                <p className="mb-2">
                                  {detail.order.shipping_charges}
                                </p>
                              </div>
                              <div className="d-flex align-items-center justify-content-between">
                                <p className="mb-2">Commission</p>
                                <p className="mb-2">
                                  {" "}
                                  {currency(detail.order.commission)}
                                </p>
                              </div>
                              <div className="d-flex align-items-center justify-content-between">
                                <p className="mb-2">Total weight</p>
                                <p className="mb-2">
                                  {" "}
                                  {detail.order.weight}kg
                                </p>
                              </div>
                              <div className="d-flex align-items-center justify-content-between">
                                <p className="mb-2">Payment Status</p>
                                <p className="mb-2 text-primary text-capitalize">
                                  {" "}
                                  {detail.order.payment_status}
                                </p>
                              </div>
                            </div>
                          </div>
                          <div className="barcode-container">
                            <QRCode
                              value={`https://entermarket.herokuapp.com/profile?showing=4&order_id=${detail.order.id}`}
                            />
                          </div>
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  ) : (
                    <div className="p-4 text-center">
                      <div className="mb-3">
                        <i
                          className="fa fa-check-circle fa-5x text-primary"
                          aria-hidden="true"
                        ></i>
                      </div>
                      <div className="font-weight-bold mb-3">
                        Payment Successful
                      </div>
                      <Link to="/profile?showing=2">Back to wallet</Link>
                    </div>
                  )}
                  {/* <Row className="justify-content-center">
                    <Col lg="4" md="6" sm="12">
                      <Button
                        color="primary"
                        className="bg-primary-gradient"
                        block
                        size="lg"
                      >
                        Share
                      </Button>
                    </Col>
                    <Col lg="4" md="6" sm="12">
                      <Button color="primary" block outline size="lg">
                        Print
                      </Button>
                    </Col>
                  </Row> */}
                  <div className="my-3 text-center">
                    <Button
                      style={{ textDecoration: "underline" }}
                      color="primary"
                      to="/"
                      tag={Link}
                      className="btn-link"
                      size="lg"
                    >
                      Back Home
                    </Button>
                  </div>
                </Col>
              ) : (
                ""
              )}

              {status === false ? (
                <Col className="d-flex justify-content-center">
                  <span className="text-center p-5  rounded">
                    <i
                      className="fa fa-times-circle fa-5x  text-danger"
                      aria-hidden="true"
                    ></i>{" "}
                    <br /> <br />
                    <strong>Payment Failed</strong>
                  </span>
                </Col>
              ) : (
                ""
              )}
            </Row>
          </Container>
        </section>

        <DefaultFooter />
      </div>
    </>
  );
}

export default OrderReceipt;
