import React from "react";
import {
  Row,
  Col,
  Breadcrumb,
  BreadcrumbItem,
  Button,
  Modal,
  ModalBody,
  FormGroup,
  Input,
  Form,
} from "reactstrap";

import stripe from "../../../assets/img/illustrations/stripe.png";
import subtract from "../../../assets/img/illustrations/subtract.png";
import red from "../../../assets/img/illustrations/quarter-circle.png";
import yellow from "../../../assets/img/illustrations/quarter-yellow.png";
import moment from "moment";
import currency from "../../../js/methods/currency";
import axios from "axios";
import { toast } from "react-toastify";
import ReadMoreReact from "read-more-react";
import ReactToPdf from "react-to-pdf";
export default function Order({ order, previous, title, closeOrder }) {
  const [modal, setModal] = React.useState(false);
  const [switchList, setswitchlist] = React.useState("1");
  const token = localStorage.getItem("user-token");
  const [message, setmessage] = React.useState("");
  const ref = React.createRef();

  const toggle = () => setModal(!modal);
  function handleListSwitch(val) {
    setswitchlist(val);
  }
  function handleReportSubmit(e) {
    e.preventDefault();
    axios
      .post(
        `${process.env.REACT_APP_API_URL}/api/reports`,
        { message: message, order_id: order.id },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        if (res.status === 201) {
          handleListSwitch("2");
          toast.success("Report submitted");
        }
      });
  }
  function handleMessage(e) {
    setmessage(e.target.value);
  }
  function handlePayment() {
    var detail = {
      amount: order.grand_total,
      order_id: order.id,
    };
    axios
      .post(`${process.env.REACT_APP_API_URL}/api/transaction/initiate`, detail, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          window.location.href = res.data.authorization_url;
        }
      });
  }
  return (
    <div>
      <div className="order-tab">
        <div className="d-flex flex-column flex-sm-row justify-content-start align-items-start align-items-sm-center">
          <Breadcrumb className="custom-breadcrumb">
            <BreadcrumbItem>
              <span onClick={closeOrder} className="text-primary">
                My Orders
              </span>
            </BreadcrumbItem>
            <BreadcrumbItem>
              <span className="text-primary" onClick={closeOrder}>
                {previous}
              </span>
            </BreadcrumbItem>
            <BreadcrumbItem active>{order.name}</BreadcrumbItem>
          </Breadcrumb>
          <span className="text-right mb-0">
            <span className="mr-4">Total weight - {order.weight}kg</span>{" "}
            <span>Items - {order.items}</span>
          </span>
        </div>

        <Row>
          <Col className="" lg="7">
            <div className="mx-auto p-3" ref={ref}>
              <div className="d-flex justify-content-between">
                <h6 className="text-capitalize">{order.name}</h6>
                <span>
                  <span className="font-weight-bold">Order ID</span> :{" "}
                  <span>{order.order_no}</span>
                </span>
              </div>
              {/* <p>
              <span>Store</span>: SPAR VI
            </p> */}
              <div className="d-flex justify-content-between align-items-center mb-1">
                <span>Destination</span>{" "}
                <span>{order.orderinfo.shipping_address}</span>
              </div>
              <div className="d-flex justify-content-between align-items-center mb-1">
                <span>Instructions</span>{" "}
                <span>{order.orderinfo.extra_instruction}</span>
              </div>

              <div className="d-flex justify-content-between align-items-center mb-1">
                <span>Status</span>{" "}
                {order.status === "pending" && !order.isScheduled ? (
                  <span className=" assigned  text-warning px-3 py-1">
                    Assigned to agent
                  </span>
                ) : (
                  ""
                )}
                {order.status === "delivered" ? (
                  <span className="delivered text-success px-3 py-1">
                    Delivered{" "}
                  </span>
                ) : (
                  ""
                )}
                {order.status === "pending" && order.isScheduled ? (
                  <span className="bg-light scheduled px-3 py-1 rounded">
                    Scheduled for delivery on{" "}
                    {moment(order.scheduled_time).format("DD/MM/YYYY")}
                  </span>
                ) : (
                  ""
                )}
              </div>

              <div>
                <small className="font-weight-bold">Payment breakdown</small>{" "}
                <br />
                <div className="d-flex justify-content-between mb-1">
                  <span>Payment status</span>{" "}
                  <span className="font-weight-bold text-muted text-capitalize">
                    {order.payment_status}
                  </span>
                </div>
                <div className="d-flex justify-content-between mb-1">
                  <span>Cost</span>{" "}
                  <span className="font-weight-bold text-muted">
                    {currency(order.total_amount)}
                  </span>
                </div>
                <div className="d-flex justify-content-between mb-1">
                  <span>Delivery</span>{" "}
                  <span className="font-weight-bold text-muted">
                    {" "}
                    {currency(order.shipping_charges)}
                  </span>
                </div>
                <div className="d-flex justify-content-between mb-1">
                  <span>Commission</span>{" "}
                  <span className="font-weight-bold text-muted">
                    {" "}
                    {currency(order.commission)}
                  </span>
                </div>
                <div className="d-flex justify-content-between mb-1">
                  <span>Grand total</span>{" "}
                  <span className="font-weight-bold text-muted">
                    {" "}
                    {currency(order.grand_total)}
                  </span>
                </div>
              </div>
              <div className="border-top d-flex justify-content-between py-2">
                <span className="font-weight-bolder">Total amount paid</span>
                <span className="font-weight-bolder">
                  {order.payment_status === "paid"
                    ? currency(order.grand_total)
                    : currency(0)}
                </span>
              </div>
            </div>
            {previous === "Scheduled" ? (
              // <Button block color="primary">
              //   Order now
              // </Button>
              ""
            ) : (
              <div className="d-flex justify-content-between mt-1">
                <Button color="danger" outline onClick={toggle}>
                  Report order
                </Button>
                {order.payment_status === "paid" ? (
                  <ReactToPdf targetRef={ref} filename="order.pdf">
                    {({ toPdf }) => (
                      <Button onClick={toPdf} color="primary">
                        Share order receipt
                      </Button>
                    )}
                  </ReactToPdf>
                ) : (
                  <Button color="primary" onClick={handlePayment}>
                    Pay now
                  </Button>
                )}
              </div>
            )}
          </Col>
          <Col className="" lg="5">
            <div className="cart-block">
              {order.orderhistories.map((item) => (
                <div className="cart-item-block" key={item.id}>
                  <img alt="..." className="product-img" src={item.image} />
                  <div style={{ flex: 1 }}>
                    <div className="d-flex justify-content-between">
                      <div>
                        <p className="product-name">{item.product_name}</p>
                        <ReadMoreReact
                          text={item.description}
                          min={4}
                          ideal={20}
                          max={80}
                        />
                        <p className="product-location">
                          <i className="fa fa-map-marker-alt mr-2" />
                          {item.store_name}
                        </p>
                      </div>
                    </div>

                    <div className="d-flex align-items-center justify-content-between mt-2">
                      <p className="product-price h5">{currency(item.price)}</p>
                      <div className="action-buttons d-flex align-items-center justify-content-between">
                        <span className="product-amount">{item.quantity}</span>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </Col>
        </Row>

        <Modal isOpen={modal} toggle={toggle} centered className="">
          <ModalBody className="p-0 position-relative">
            {switchList === "1" ? (
              <Form onSubmit={(e) => handleReportSubmit(e)}>
                <div className="p-3 p-md-5 report-body">
                  <div className="d-flex justify-content-between w-100 font-weight-bold mb-4">
                    <span className="text-danger">
                      Report on -{" "}
                      <small className="text-muted">
                        Order ID : {order.order_no}
                      </small>
                    </span>
                    <span>Monthly order</span>
                  </div>
                  <small>
                    We are sorry your order did not go as you desired. Please
                    tell us the issue with this order{" "}
                  </small>{" "}
                  <br />
                  <FormGroup>
                    <Input
                      type="textarea"
                      name="text"
                      id="exampleText"
                      required
                      value={message}
                      onChange={(e) => handleMessage(e)}
                      placeholder={`Order ID : ${order.order_no}`}
                    />
                  </FormGroup>
                  <div className="d-flex justify-content-between align-items-center">
                    <a
                      target="_blank"
                      href="https://tawk.to/chat/61695f68f7c0440a591e5c2b/1fi1ppc8n"
                      rel="noreferrer"
                      type="button"
                      className="px-md-4 text-success"
                    >
                      Chat with us
                    </a>
                    <Button color="success" className="px-md-5" type="submit">
                      Submit report
                    </Button>
                  </div>
                </div>
                <img alt="stripe" src={stripe} className="modal-stripe" />
                <img alt="subtract" src={subtract} className="modal-subtract" />
              </Form>
            ) : (
              ""
            )}

            {switchList === "2" ? (
              <div>
                <div className="p-3 p-md-5 list-body d-flex justify-content-center align-items-center ">
                  <div className="text-center">
                    <i
                      className="fas fa-check-circle fa-5x text-success"
                      aria-hidden="true"
                    ></i>{" "}
                    <br />
                    <span className="font-weight-bold">
                      Your report has been successfully sent and a response will
                      be provided via your email address.
                    </span>
                  </div>
                </div>
                <img alt="red" src={red} className="modal-red" />
                <img alt="yellow" src={yellow} className="modal-yellow" />
              </div>
            ) : (
              ""
            )}
          </ModalBody>
        </Modal>
      </div>
    </div>
  );
}
